  import Head from "../public/head.vue"
	export default {
		data() {
			return {
				activeIndex:'',
        editSearch:'搜索',
        content:'',
			};
		},
		components:{
			Head
		},
		methods:{
      getVal(msg){
        this.content = msg
      },
      searchHome(){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: this.content,
            type:0
          }
        })
      },
			toHelphome(){
			},
			handleOpen(){
			},
			handleClose(){
			},
      policy(router){
        this.$router.push(router)
      }
		},
    mounted() {
      window.scroll(0,0)
    }

  }



